@-ms-viewport {
  width: device-width; }

.page {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%; }
  .page__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column; }
  .page__content {
    flex-grow: 1; }
  .page__footer-wrapper {
    flex-shrink: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #fff;
  margin: 0;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  color: #231F20; }

[tabindex='-1']:focus {
  outline: none !important; }

[role='button'] {
  cursor: pointer; }

input[type='search'] {
  -webkit-appearance: textfield; }

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; }

a svg,
button svg {
  pointer-events: none; }

img {
  max-width: 100%;
  height: auto; }

.page-header__inner {
  display: block; }

.features__header {
  background-color: #6767e0;
  color: #fff; }

.features__list {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (min-width: 768px) {
    .features__list {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex; } }

.features__item {
  display: block;
  background-image: svg-load("../img/friendship.svg", fill=#000, stroke=#fff); }
  @media (min-width: 768px) {
    .features__item {
      width: 33.333334%; } }

.btn {
  display: inline-block;
  padding: 5px;
  border: none;
  line-height: 1;
  font-size: 14px;
  font-weight: 400;
  background-color: #fff; }
  .btn:hover, .btn:focus {
    background-color: #F2F2F2; }
